import React from "react";
import Icon from "./Icon";

const ModuleCard = ({ module }: { module: any }) => {
  const getStatusIcon = () => {
    if (module.status === "completed") return "check";
    if (module.status === "active") return "arrow";
    return "lock";
  };

  return (
    <div className="flex items-center justify-between p-2 bg-background-1 rounded">
      <span className="text-neutral-600">{module.name}</span>
      <Icon name={getStatusIcon()} />
    </div>
  );
};

export default ModuleCard;
