import React from "react";
import { levels } from "../utils/progressData";
import NivelCard from "../components/NivelCard";

const ProgressPage = () => {
  return (
    <div className="p-6 space-y-6">
      <h1 className="text-primary-900 font-bold text-2xl">Mi Progreso</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {levels.map((level) => (
          <NivelCard key={level.id} level={level} />
        ))}
      </div>
    </div>
  );
};

export default ProgressPage;
